import React, { useState } from 'react';
import { Link as GatsbyLink } from 'gatsby';
import { AppBar, Toolbar, Typography, Link, Button, Tooltip, Icon, FormGroup, FormControlLabel, styled } from '@material-ui/core';
import { Theme, makeStyles } from '@material-ui/core/styles';
import {
  CreateCSSProperties,
} from '@material-ui/styles/withStyles';
import Switch from '@material-ui/core/Switch';
import { FC } from '../../util';

const useStyles = makeStyles((theme: Theme) => ({
    root: {
        backgroundColor: theme.palette.background.default,
        color: theme.palette.text.primary,
        minHeight: '48px',
        boxShadow:'0px 2px 4px -1px rgb(0 0 0 / 12%), 0px 1px 1px 0px rgb(0 0 0 / 4%), 0px 1px 10px 0px rgb(0 0 0 / 8%)'
        
    },
    title: {
        flexGrow: 1
    },
    link: {
        textDecorationLine: 'none',
        "&:hover":{
            textDecorationLine: 'none',
        }
    }
}));

export interface HeaderProps {
    switchTheme: () => void;
    siteTitle?: string;
}

const Header: FC<HeaderProps> = (props) => {
    const [showOn, setShowOn] = useState<boolean>(false);
    const styles = useStyles();

    const onSwitch = (): void => {
        setShowOn((prev : boolean) => !prev);
        props.switchTheme();
    };

    const MaterialUISwitch = styled(Switch)(({ theme  }) : CreateCSSProperties => ({
        width: 62,
        height: 34,
        padding: 7,
        "& .MuiSwitch-switchBase": {
          margin: 1,
          padding: 0,
          transform: "translateX(6px)",
          "&.Mui-checked": {
            color: "#fff",
            transform: "translateX(22px)",
            "& .MuiSwitch-thumb:before": {
              backgroundImage: `url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" height="20" width="20" viewBox="0 0 20 20"><path fill="${encodeURIComponent(
                "#fff"
              )}" d="M4.2 2.5l-.7 1.8-1.8.7 1.8.7.7 1.8.6-1.8L6.7 5l-1.9-.7-.6-1.8zm15 8.3a6.7 6.7 0 11-6.6-6.6 5.8 5.8 0 006.6 6.6z"/></svg>')`
            },
            "& + .MuiSwitch-track": {
              opacity: 1,
              backgroundColor: theme.palette?.type === "dark" ? "#8796A5" : "#aab4be"
            }
          }
        },
        "& .MuiSwitch-thumb": {
          backgroundColor: theme.palette.type === "dark" ? "#003892" : "#001e3c",
          width: 32,
          height: 32,
          "&:before": {
            content: "''",
            position: "absolute",
            width: "100%",
            height: "100%",
            left: 0,
            top: 0,
            backgroundRepeat: "no-repeat",
            backgroundPosition: "center",
            backgroundImage: `url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" height="20" width="20" viewBox="0 0 20 20"><path fill="${encodeURIComponent(
              "#fff"
            )}" d="M9.305 1.667V3.75h1.389V1.667h-1.39zm-4.707 1.95l-.982.982L5.09 6.072l.982-.982-1.473-1.473zm10.802 0L13.927 5.09l.982.982 1.473-1.473-.982-.982zM10 5.139a4.872 4.872 0 00-4.862 4.86A4.872 4.872 0 0010 14.862 4.872 4.872 0 0014.86 10 4.872 4.872 0 0010 5.139zm0 1.389A3.462 3.462 0 0113.471 10a3.462 3.462 0 01-3.473 3.472A3.462 3.462 0 016.527 10 3.462 3.462 0 0110 6.528zM1.665 9.305v1.39h2.083v-1.39H1.666zm14.583 0v1.39h2.084v-1.39h-2.084zM5.09 13.928L3.616 15.4l.982.982 1.473-1.473-.982-.982zm9.82 0l-.982.982 1.473 1.473.982-.982-1.473-1.473zM9.305 16.25v2.083h1.389V16.25h-1.39z"/></svg>')`
          }
        },
        "& .MuiSwitch-track": {
          opacity: 1,
          backgroundColor: theme.palette.type === "dark" ? "#8796A5" : "#aab4be",
          borderRadius: 20 / 2
        },
      }));

    return (
        <AppBar component="header" position="static" className={styles.root}>
            <Toolbar>
                <Typography variant="h6" className={styles.title}>
                    <Link to="/" component={GatsbyLink} color="inherit" className={styles.link}>
                    {/* <img src={logo} width="40" height="40" /> */}
                        {props.siteTitle || ''}
                    </Link>
                </Typography>
                <FormGroup>
                    <FormControlLabel 
                        control={<MaterialUISwitch />}
                        label={null}
                        onClick={onSwitch}
                        checked={showOn}
                        // label="MUI switch"
                    />
                </FormGroup>
                {/* <Tooltip title="switch theme">
                    <Switch onChange={onSwitch} checked={showOn} color="default" />
                </Tooltip> */}
            </Toolbar>
            
        </AppBar>
    );
};

export default Header;
